import axios, { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useQuery } from 'react-query'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { LikedClubPreviewResponse } from 'src/responses/likeSortedClubs'

const getLikeSortedClubs = (token: string) => async () => {
  const response = await axios.get<LikedClubPreviewResponse>(
    `${process.env.REACT_APP_APIHOST}/search/clubs?count=${encodeURIComponent(12)}&order=LIKE_DES`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

export const useGetLikeSortedClubs = () => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  return useQuery<LikedClubPreviewResponse>('likedClubs', getLikeSortedClubs(token), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
