import { Link } from 'react-router-dom'
import Slider from 'react-slick'

import { Box, Button, Container, type Theme, Typography, useTheme } from '@mui/material'

import RecruitNoticePreview, { type RecruitNoticePreviewProps } from 'src/components/RecruitNoticePreview'

import { useGetDefaultRecruits } from 'src/hooks/api/search'

import 'slick-carousel/slick/slick-theme.css'
import 'slick-carousel/slick/slick.css'

interface RecruitingClubType {
  theme: Theme
  isMqMatched: boolean
}

const today = new Date()

const RecruitingClubs: React.FC<RecruitingClubType> = ({ theme, isMqMatched }) => {
  const count = isMqMatched ? 20 : 8
  const isRecruitingOnly = true
  const { data: response } = useGetDefaultRecruits(count, isRecruitingOnly)
  const recruits = response?.recruits

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: theme.spacing(2, isMqMatched ? 0 : 2)
      }}
    >
      {recruits?.length ? (
        <Typography variant="h3" component="h2">
          현재 모집 중인 동아리예요
        </Typography>
      ) : (
        <Typography flexDirection={'row'} variant="h4" mt={2}>
          모집중인 동아리가 없어요 :(
        </Typography>
      )}
      <Container
        sx={{
          padding: theme.spacing(1, 0, 5)
        }}
      >
        {recruits && <RecruitCarousel recruits={recruits} />}
      </Container>
      <Link to="search/recruit" style={{ textDecoration: 'none' }}>
        <Button variant="contained" size="large">
          전체 모집공고 보기
        </Button>
      </Link>
    </Box>
  )
}

export default RecruitingClubs

interface RecruitCarouselProps {
  recruits: RecruitNoticePreviewProps[]
}

const RecruitCarousel: React.FC<RecruitCarouselProps> = ({ recruits }) => {
  const {
    breakpoints: {
      values: { lg, md }
    },
    spacing
  } = useTheme()

  const responsive = [
    {
      breakpoint: lg,
      settings: {
        slidesToShow: Math.min(recruits.length, 2),
        slidesToScroll: Math.min(recruits.length, 2)
      }
    },
    {
      breakpoint: md,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]

  return (
    <Slider
      dots
      infinite
      slidesToShow={Math.min(recruits.length, 2)}
      slidesToScroll={Math.min(recruits.length, 2)}
      autoplay
      responsive={responsive}
      arrows={false}
    >
      {recruits.map(club => (
        <Box key={club.id} sx={{ padding: spacing(0, 1) }}>
          <RecruitNoticePreview {...club} />
        </Box>
      ))}
    </Slider>
  )
}
