import { useState } from 'react'
import { useRecoilState } from 'recoil'

import { Box, Card, CardActionArea, CardContent, Chip, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'

import { type ClubBelonging } from 'src/data/belonging'

import LikeBtn from 'src/components/LikeBtn'

import { useDeleteLikeClub, usePostLikeClub } from 'src/hooks/api/club'
import { isLikeBtnClickedAtom } from 'src/recoil/atom'
import { stringToColor } from 'src/utils'

export interface ClubPreviewProps {
  id: number
  name: string
  category: string
  belonging: ClubBelonging
  description: string
  isLiked: boolean
}

function ClubPreview({ id, name, category, belonging, description, isLiked }: ClubPreviewProps) {
  const clubId = Number(id)

  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
  const postLikeClubMutation = usePostLikeClub(clubId)
  const deleteLikeClubMutation = useDeleteLikeClub(clubId)

  const [isLike, setIsLike] = useState(isLiked)
  const [, setIsLikeBtnClicked] = useRecoilState(isLikeBtnClickedAtom)

  const handleLike = async (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault()
    try {
      {
        isLike ? await deleteLikeClubMutation.mutateAsync() : await postLikeClubMutation.mutateAsync()
      }
      setIsLike(!isLike)
      setIsLikeBtnClicked(true)
    } catch (error) {}
  }

  return (
    <Card>
      <CardActionArea href={`/club/${id}`} sx={{ display: 'flex', flexDirection: matches ? 'column' : 'row' }}>
        <CardContent sx={{ flex: 1, width: '100%', padding: isMobile ? 1.3 : '' }}>
          <Box display="flex" flexDirection="row" alignItems="center" justifyContent="space-between">
            <Typography gutterBottom variant="h5" component="div">
              {name}
            </Typography>
            <Box sx={{ marginTop: theme.spacing(-0.5) }}>
              <LikeBtn handleLike={handleLike} isLike={isLike} />
            </Box>
          </Box>
          <Typography
            variant="body2"
            color="text.secondary"
            whiteSpace="nowrap"
            overflow="hidden"
            textOverflow="ellipsis"
          >
            {description}
          </Typography>
          <Stack direction="row" justifyContent="flex-start" spacing={1} sx={{ marginTop: theme.spacing(1) }}>
            {belonging && (
              <Chip label={belonging} size="small" sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }} />
            )}
            {category && (
              <Chip label={category} size="small" sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }} />
            )}
          </Stack>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}

export default ClubPreview
