import { Box, Typography, useTheme } from '@mui/material'

import ClubList from 'src/components/ClubList'

import { useGetLikeSortedClubs } from 'src/hooks/api/likeSortedClubs'

const MostLikedClubs = () => {
  const theme = useTheme()
  const { data } = useGetLikeSortedClubs()
  return (
    <Box
      component="section"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: theme.spacing(2, 0),
        padding: theme.spacing(5, 4),
        [theme.breakpoints.down('sm')]: {
          padding: theme.spacing(2, 4)
        }
      }}
    >
      {data?.clubs && (
        <Typography variant="h3" component="h2">
          현재 인기 있는 동아리예요
        </Typography>
      )}
      <Box sx={{ justifyContent: 'space-around', alignItems: 'center', maxWidth: '1220px' }}>
        {data?.clubs && <ClubList clubs={data?.clubs} />}
      </Box>
    </Box>
  )
}

export default MostLikedClubs
