import axios, { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useQuery } from 'react-query'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { SearchAPIResponse, SearchRecruitsResponseBody } from 'src/responses/search'

const getDefaultRecruitsAPICall = (count: number, isReruitingOnly?: boolean) => async () => {
  const response = await axios.get<SearchRecruitsResponseBody>(
    `${process.env.REACT_APP_APIHOST}/search/recruits?isRecruiting=${isReruitingOnly ?? false}`,
    {
      params: { count: count }
    }
  )
  return response.data
}

export const useGetDefaultRecruits = (count: number, isReruitingOnly?: boolean) => {
  const { enqueueSnackbar } = useSnackbar()

  return useQuery<SearchRecruitsResponseBody>('defaultRecruits', getDefaultRecruitsAPICall(count, isReruitingOnly), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

const getClubsByParam = (apiParams: URLSearchParams, token: string) => async () => {
  const response = await axios.get<SearchAPIResponse>(`${process.env.REACT_APP_APIHOST}/search/clubs`, {
    params: apiParams,
    headers: { Authorization: token }
  })
  return response.data
}

export const useGetClubsByParam = (searchParams: URLSearchParams) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  searchParams.set('count', '24')
  const response = useQuery<SearchAPIResponse>(
    ['clubsByParam', searchParams.toString()],
    getClubsByParam(searchParams, token),
    {
      onError: error => {
        if (!(error instanceof AxiosError)) {
          enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, {
            variant: 'error'
          })
        }
      }
    }
  )
  return response
}
